"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Alert } from "@/components/ui/alert"

interface Client {
  name: string
  email: string
  phone: string
  notes: string
  documents: File[]
}

export default function AdminDashboard() {
  const [clients, setClients] = useState<Client[]>([])
  const [newClient, setNewClient] = useState<Client>({
    name: "",
    email: "",
    phone: "",
    notes: "",
    documents: []
  })
  const [message, setMessage] = useState("")

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setNewClient({
        ...newClient,
        documents: Array.from(e.target.files)
      })
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!newClient.name || !newClient.email) {
      setMessage("Por favor complete los campos requeridos")
      return
    }

    setClients([...clients, newClient])
    setNewClient({
      name: "",
      email: "",
      phone: "",
      notes: "",
      documents: []
    })
    setMessage("Cliente agregado exitosamente")
  }

  return (
    <div className="min-h-screen bg-white p-8">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-4xl font-bold mb-8">Panel de Administración</h1>

        <Card className="p-6 mb-8">
          <h2 className="text-2xl font-semibold mb-6">Agregar Nuevo Cliente</h2>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <Label htmlFor="name">Nombre *</Label>
              <Input
                id="name"
                value={newClient.name}
                onChange={(e) => setNewClient({ ...newClient, name: e.target.value })}
                placeholder="Nombre del cliente"
                required
              />
            </div>

            <div>
              <Label htmlFor="email">Email *</Label>
              <Input
                id="email"
                type="email"
                value={newClient.email}
                onChange={(e) => setNewClient({ ...newClient, email: e.target.value })}
                placeholder="email@ejemplo.com"
                required
              />
            </div>

            <div>
              <Label htmlFor="phone">Teléfono</Label>
              <Input
                id="phone"
                value={newClient.phone}
                onChange={(e) => setNewClient({ ...newClient, phone: e.target.value })}
                placeholder="Número de teléfono"
              />
            </div>

            <div>
              <Label htmlFor="notes">Notas</Label>
              <Textarea
                id="notes"
                value={newClient.notes}
                onChange={(e) => setNewClient({ ...newClient, notes: e.target.value })}
                placeholder="Notas adicionales sobre el cliente"
              />
            </div>

            <div>
              <Label htmlFor="documents">Documentos</Label>
              <Input
                id="documents"
                type="file"
                onChange={handleFileChange}
                multiple
                className="mt-1"
              />
            </div>

            <Button type="submit" className="w-full">
              Agregar Cliente
            </Button>
          </form>

          {message && (
            <Alert className="mt-4">
              {message}
            </Alert>
          )}
        </Card>

        <Card className="p-6">
          <h2 className="text-2xl font-semibold mb-6">Clientes Registrados</h2>
          <div className="space-y-4">
            {clients.map((client, index) => (
              <Card key={index} className="p-4">
                <h3 className="font-semibold">{client.name}</h3>
                <p className="text-gray-600">{client.email}</p>
                {client.phone && <p className="text-gray-600">{client.phone}</p>}
                {client.notes && <p className="text-gray-600 mt-2">{client.notes}</p>}
                {client.documents.length > 0 && (
                  <div className="mt-2">
                    <p className="font-semibold">Documentos:</p>
                    <ul className="list-disc list-inside">
                      {client.documents.map((doc, i) => (
                        <li key={i} className="text-gray-600">
                          {doc.name}
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </Card>
            ))}
          </div>
        </Card>
      </div>
    </div>
  )
}
